/* Fonts */
/* Archivo Black */
/* Archivo Narrow */

/* border-box for everything */
* {
    box-sizing: border-box;
}

/* Setting CSS color variables and root font size */
:root {
    font-size: 16px;

    --text-primary: #FEFEFE;
    --background: #002626;
    --banner-back: #0E4749;
    --accent: #D3F6DB;
    --dark-green: #277D0F;
    --light-green: #477E19;
}
body {
    font-family: 'Archivo Narrow', Helvetica, sans-serif;
    color: var(--text-primary);
}

h1 {
    font-family: 'Archivo Black', Helvetica, sans-serif;
    font-size: 4rem;
}

h2 {
    font-size: 2.5rem;
}

h3 {
    font-size: 2.5rem;
}

h4 {
    font-size: 2rem;
    background-color: var(--text-primary);
    color: var(--background);
    padding: 0.5rem;
    border-radius: 5px;
}

h5 {
    font-size: 1.5rem;
    padding: 0.5rem;
}

td {
    font-size: 1.25rem;
    padding: 0.25rem 0;
}

button {
    font-family: 'Archivo Black', Helvetica, sans-serif;
    font-size: 1rem;
}

label, input {
    font-size: 1.5rem;
    font-family: 'Archivo Narrow', Helvetica, sans-serif;
}

footer p {
    color: var(--accent);
}

td button, .match-id {
    font-size: 1rem;
    font-family: 'Archivo Narrow', Helvetica, sans-serif;
}

p {
    font-size: 1rem;
    line-height: 1.7;
    font-weight: 300;
    font-family: 'Montserrat', Helvetica, sans-serif;
}

a {
    text-decoration: none;
    color: var(--text-primary);
    font-size: 1.5rem;
}

.score-input {
    font-size: 2rem;
    font-family: 'Archivo Narrow', Helvetica, sans-serif;
}

/* Mobile */
@media (max-width: 500px) {
    h3 {
        font-size: 2rem;
    }
    .team-info h3 {
        font-size: 1.25rem;
    }
    h4 {
        font-size: 1rem;
    }
    td {
        font-size: 1rem;
        padding: 0.25rem 0;
    }
}
.btn-primary {
    padding: 1rem;
    border-radius: 15px;
    border: none;
    background-color: var(--text-primary);
}

.btn-primary:hover {
    background-color: var(--accent);
    cursor: pointer;
}

.btn-increment {
    border: none;
    background-color: var(--text-primary);
}

.btn-increment-l {
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
}

.btn-increment-r {
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}

.btn-increment:hover {
    background-color: var(--accent);
    cursor: pointer;
}

.icon-link {
    text-decoration: none;
    color: var(--accent);
    font-size: 1.5rem;
}

.icon-link:hover {
    color: var(--text-primary)
}

td .btn-primary {
    padding: 0.25rem 0.75rem;
}

td button:hover {
    background-color: var(--accent);
    cursor: pointer;
}

.match-id {
    color: var(--background);
    background-color: var(--accent);
    border-radius: 15px;
    padding: 0.5rem;
    margin: 0 1rem;
}

.instructions-icon {
    color: var(--text-primary);
    font-size: 8rem;
    padding: 1rem;
    margin-bottom: 1rem;
}
.main-header, .match-hist-header {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    background-color: var(--banner-back);
    border-radius: 5px;
}

.main-header h1 {
    margin-bottom: 0.5rem;
}

.main-header p {
    padding: 0.25rem;
}

.accent-color {
    color: var(--accent);
}

.main-footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0.75rem;
    background-color: var(--banner-back);
}

.main-footer a {
    margin: 0 0.5rem;
}
.pitch-container-background {
    background: url(/come-on-you-team/static/media/pitch.9f55767a.png); 
    background-size: cover;
    height: 0;
    padding-top: calc(812 / 1280 * 100%);
    position: relative;
}

.pitch-container-inside {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.pitch-section-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;  
}

.pitch-header {
    text-align: center;
    padding: 1rem;
}
.player-name-label {
    padding: 1rem;
}

.name-input {
    padding: 0.25rem;
    border-radius: 5px;
    border: none;
}

.player-entry-form {
    padding: 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.team-size-section {
    padding: 1rem;
    display: flex;
    justify-content: center;
}

.team-size-section .btn-primary {
    margin-left: 1rem;
}

.increment-container {
    display: flex;
}

.team-size-section h5 {
    align-self: center;
}

/* Mobile */
@media (max-width: 600px) {
    .form-section {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .player-name-label {
        padding: 0.5rem;
    }
}

.team-info, .team-entry-form, .score-section {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: var(--banner-back);
    padding: 0.5rem;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}

.team-name-label {
    display: hidden;
}
.form-section {
    padding: 0 1rem;
}
.form-section input {
    text-align: center;
}

.score-input {
    text-align: center;
    padding: 0;
}

/* Mobile */
@media (max-width: 650px) { 
    .team-entry-form {
        flex-direction: column;
    }

    .form-section input {
        margin-bottom: 0.5rem;
    }

}

.match-options-section {
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
}

.match-options-section button {
    margin: 0 2rem;
}
.match-hist-section {
    display: flex;
    flex-direction: column;
}

.match-hist-header {
    background-color: var(--background);
}

.match-hist-table {
    align-self: center;
    text-align: center;
    background-color: var(--light-green);
}

tr:nth-child(4n+1) {
    background-color: var(--dark-green);
}

tr:nth-child(4n+2) {
    background-color: var(--dark-green);
}

tr td:nth-child(1), tr td:nth-child(5) {
    width: 200px;
}

th {
    color: var(--text-primary);
    background-color: var(--background);
    margin: 1rem 0.5rem;
}

td {
    padding: 1rem 0.5rem;
}

.loader {
    display: flex;
    justify-content: center;
    padding: 1.5rem;
}

@media (max-width: 500px) {
    td, th {
        padding: 1rem 0rem;
    }

    tr td:nth-child(1), tr td:nth-child(5) {
        width: 150px;
    }
}
.four-oh-four {
    text-align: center;
    padding: 3rem 0;
    background-color: var(--light-green);
}

nav {
    display: flex;
    justify-content: space-around;
    background-color: var(--background);
    border-top: 2px solid var(--accent);
    border-bottom: 5px solid var(--accent);
}

nav a {
    padding: 0.75rem;
    width: 50vw;
    display: flex;
    justify-content: center;
}

nav a:hover {
    background-color: var(--dark-green);
}
.instructions-section {
    color: var(--accent);
    text-align: center;
    border-bottom: 5px solid var(--accent);
    background-color: var(--banner-back);
}

.instructions-header {
    margin-bottom: 1rem;
    color: var(--text-primary);
}

.instructions-header h2 {
    padding: 0.75rem;
}

.instructions-card-container {
    color: var(--accent);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2rem;
    max-width: 960px;
    margin: 0 auto;
    padding: 0 2rem;
}

.instructions-card {
    margin-bottom: 1rem;
}

.instructions-card p {
    padding: 0.25rem 0.5rem;
    text-align: left;
}

.instructions-section button {
    margin: 0.75rem 0rem;
}

/* Collapse to 1 column for smaller screens */
@media (max-width: 500px) { 
    .instructions-card-container {
        grid-template-columns: 1fr;
    }
}

body {
    /* background-color: var(--background); */
    background-image: url(/come-on-you-team/static/media/field2.adf8ea5c.jpg);
    background-size: cover;
}

.main-app {
    background-color: var(--background);
    max-width: 1000px;
    margin: 0 auto;
}
