.match-hist-section {
    display: flex;
    flex-direction: column;
}

.match-hist-header {
    background-color: var(--background);
}

.match-hist-table {
    align-self: center;
    text-align: center;
    background-color: var(--light-green);
}

tr:nth-child(4n+1) {
    background-color: var(--dark-green);
}

tr:nth-child(4n+2) {
    background-color: var(--dark-green);
}

tr td:nth-child(1), tr td:nth-child(5) {
    width: 200px;
}

th {
    color: var(--text-primary);
    background-color: var(--background);
    margin: 1rem 0.5rem;
}

td {
    padding: 1rem 0.5rem;
}

.loader {
    display: flex;
    justify-content: center;
    padding: 1.5rem;
}

@media (max-width: 500px) {
    td, th {
        padding: 1rem 0rem;
    }

    tr td:nth-child(1), tr td:nth-child(5) {
        width: 150px;
    }
}